___CSS_LOADER_EXPORT___.push([module.id, `.ship {
  cursor: pointer;
  display: flex;
}

.ship.placing {
  box-shadow: 0 0 1rem 0.25rem var(--color-ship-placing);
}

.ship-node {
  background-color: var(--color-ship-bg);
  border-radius: 0.25rem;
}

.ship-preview-node {
  background-color: var(--color-ship-valid);
  border-radius: 0.25rem;
}

.ship-preview-invalid {
  background-color: var(--color-ship-invalid);
  border-radius: 0.25rem;
}

.undiscovered-ship-nodes-hidden .ship-node:not(.sunk) {
  background-color: transparent;
}

.face-right {
  flex-direction: row;
}

.face-left {
  flex-direction: row-reverse;
}

.face-up {
  flex-direction: column-reverse;
}

.face-down {
  flex-direction: column;
}
