___CSS_LOADER_EXPORT___.push([module.id, `html {
  --black: #000;
  --dark-gray-2: #181818;
  --dark-gray-1: #333;
  --gray: #383838;
  --light-gray-1: #555;
  --light-gray-2: #666;
  --light-gray-3: #888;
  --white: #ccc;

  --red: #d33;
  --light-green: #3d3;
  --blue: #33d;
  --pale-blue: #68f;
  --paler-blue: #46c;

  --color-bg-primary: var(--dark-gray-2);
  --color-bg-secondary: var(--dark-gray-1);
  --color-text-primary: var(--white);

  --color-menu-bg: var(--color-bg-secondary);
  --color-input-bg: var(--color-bg-primary);
  --color-input-fg: var(--color-fg-primary);
  --color-button-1-bg: var(--light-gray-1);
  --color-button-1-bg-hover: var(--light-gray-2);
  --color-border-focus: var(--pale-blue);

  --color-board-bg: var(--blue);
  --color-board-border: var(--paler-blue);
  --color-active-area-border: var(--light-green);
  --color-ship-bg: var(--light-gray-3);
  --color-ship-outline: var(--white);
  --color-ship-placing: var(--pale-blue);
  --color-ship-valid: var(--light-green);
  --color-ship-invalid: var(--red);
}
